body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0px;
}

.navbar {
  background-color: #333;
  color: white;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  cursor: pointer;
  padding: 0 1rem;
}

.site-title {
  font-size: 2rem;
}

.navbar ul {
  padding: 0px;
  margin: 0px;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.navbar a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.navbar li:active {
  background-color: #555;
}

.navbar li:hover {
  background-color: #777;
}

#LoginForm {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #121735;
  display: flex;
  justify-content: center;
  align-items: center;
}

#backdrop {
  z-index: 1;
  position: absolute;
  top: 0vh;
  bottom: 0vh;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-evenly;
  background-color: rgba(18, 23, 53, 0.5);
  opacity: 0.95;
  backdrop-filter: blur(10px);
}


#LoginModal {
  z-index: 2;
  background-color: rgba(18, 23, 53, 0.9);
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: #09b6fe;
}

#loginText {
  font-size: 2em;
  font-weight: bold;
}

#loginError {
  font-size: 0.8em;
  color: red;
}

form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px;
}
#LoginModal form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

#modalInputs input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  opacity: 0.7;
}

#modalInputs input:focus {
  opacity: 1;
}

#modalButtons {
  width: 100%;
  display: flex;
  justify-content: center;
}

#modalButtons button {
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: rgba(65, 163, 138, 0.8);
  width: 100%;
  margin-bottom: 10px;
}

#modalButtons button:first-of-type {
  background-color: #09b6fe;
  color: white;
  padding: 10px 20px;
}

#modalButtons button:last-of-type {
  background-color: #f2fbff;
  color: #09b6fe;
  padding: 10px 20px;
}



.search-input {
  padding: 5px 10px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
}



.search-button{
  background-color:transparent;
  border: none;
}

.search-button img{
  width: 20px;       /* Width of the image */
  height: 20px;      /* Height of the image */
  
}

#create-account-container {
  display: flex;
  flex-direction: column;
  width: 50%; /* Adjust the width as necessary */
  margin: auto;
  padding: 20px;
  background-color: #fff; /* White background for the form */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Soft shadow around the form */
  border-radius: 8px; /* Rounded corners */
  positon: absolute;
}

#create-account-title {
  text-align: center;
  font-size: 24px; /* Large font size for the title */
  color: #333; /* Dark grey color for the text */
  margin-bottom: 20px; /* Space below the title */
}


#create-account-form {
  display: flex;
  flex-direction: column;
  gap: 15px; /* Space between form items */
  background-color: white;
  justify-content: center;
  align-items: center;
}

.form-section {
  /* Your styles for each form section */
}

.form-group {
  display: flex;
  flex-direction: column; /* Stack labels on top of inputs */
}

.form-input {
  font-size: 16px; /* Larger font size for readability */
  padding: 10px; /* Padding inside the inputs */
  margin-bottom: 10px; /* Space below each input */
  border: 1px solid #ccc; /* Light grey border */
  border-radius: 4px; /* Slightly rounded borders for the inputs */
}

#create-account-button {
  background-color: #17a2b8; /* Teal background color for the button */
  color: white; /* White text color */
  padding: 15px 20px; /* Padding inside the button */
  font-size: 18px; /* Larger font size for the button text */
  border: none; /* No border for the button */
  border-radius: 4px; /* Rounded corners for the button */
  cursor: pointer; /* Cursor to indicate it's clickable */
  margin-top: 10px; /* Space above the button */
}

#validation-errors .error-message {
  color: red; /* Red color for error messages */
  font-size: 14px; /* Smaller font size for error messages */
  margin-top: 5px; /* Space above error messages */
  align: center;
}



.edit-profile-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem;
  gap: 2rem; /* Adds space between the profile section and the form */
  background-color: #f8f8f8; /* Background color of the whole page */
}

.profile {
  background: #ffffff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center; /* Centering the content inside the aside */
  width: 20%; /* Adjust the width of the sidebar */
}

.profile-picture {
  border-radius: 50%;
  width: 50%; /* Adjust the size as needed */
  margin-bottom: 1rem; /* Adds space below the image */
}

.edit-form {
  background: #ffffff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 70%; /* Adjust the width of the form */
  text-align: center; /* Aligns the text to the left */
}

.edit-form h1 {
  margin-bottom: 1rem; /* Adds space below the header */
}

.error-message {
  color: red;
  margin-bottom: 1rem; /* Adds space below the error message */
}

.info {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two columns of equal width */
  gap: 1rem; /* Space between grid items */
  align-items: center; /* Aligns items vertically */
}

.info label {
  display: block;
  margin-bottom: 0.5rem; /* Adds space below the label */
}

.info input[type="file"],
.info input[type="text"],
.info input[type="date"],
.info input[type="number"],
.info select,
.info textarea {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem; /* Adds space below the input */
  border: 1px solid #ccc;
  border-radius: 4px;
}

.info button {
  grid-column: 1 / -1; /* Button spans across all columns */
  padding: 0.5rem 1rem;
  background-color: #09b6fe;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 1rem; /* Adds space above the button */
}